body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Main {
  width: 100%;
  min-height: 70vh;
  align-items: center;
}
.NavBar {
  width: 100%;
  height: 80px;
  font-size: calc(10px + 4vmin);
}

.Headers {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: 8%;
  -webkit-column-gap: 8%;
          column-gap: 8%;
}

.Headers a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  margin: 20px;
}
.Intro {
  height: calc(100vh - 78px);
  display: flex;
  justify-content: center;
}

.Intro h1 {
  font-size: 75px;
  font-weight: 650;
  top: calc(100vh * 0.2);
  position: relative;
}

.Skills {
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #282c34;
}

.Skills h1 {
  font-size: 60px;
  font-weight: 600;
  margin-top: 140px;
  margin-bottom: -15px;
}

.Skills h2 {
  font-size: 45px;
  font-weight: 500;
  padding: 30px;
  margin-bottom: -40px;
}

.Skills p {
  font-size: 30px;
  font-weight: 450;
  padding: 30px;
  margin-top: 0px;
}  
